import React from 'react';
import classname from 'classnames';

export enum ButtonVariant {
    default = 'default',
    primary = 'primary',
    secondary = 'secondary',
    success = 'success',
    info = 'info',
    danger = 'danger',
    warning = 'warning',
}

export enum ButtonSize {
    sm,
    md,
    lg,
}

export interface IProps {
    outlined?: Boolean;
    onClick?: () => void;
    disabled?: Boolean;
    label: string,
    variant: ButtonVariant
    size: ButtonSize
}

export const Button: React.FC<IProps> = ({ 
    label,
    outlined = false,
    size = ButtonSize.md,
    variant = ButtonVariant.default,
    ...props
 }) => {
    return (
        <button className={
            classname({
                'font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg mr-1 mb-1 ease-linear transition-all duration-150' :  true,
                // Outline none
                'bg-gray-600 text-white active:bg-gray-700 hover:bg-gray-700' : ButtonVariant.default == variant,
                'bg-teal-300 text-white active:bg-teal-500 hover:bg-teal-500' : ButtonVariant.primary == variant,
                'bg-orange-400 text-white active:bg-oraginge-600 hover:bg-oraginge-600' : ButtonVariant.secondary == variant,
                'bg-teal-400 text-white active:bg-teal-500 hover:bg-teal-500' : ButtonVariant.success == variant,
                'bg-cyan-300 text-white active:bg-cyan-500 hover:bg-cyan-500' : ButtonVariant.info == variant,
                'bg-amber-400 text-white active:bg-amber-500 hover:bg-amber-500' : ButtonVariant.warning == variant,
                'bg-red-400 text-white active:bg-red-500 hover:bg-red-500' : ButtonVariant.danger == variant,
            
                // Outline
                'outline outline-2 bg-transparent' : outlined,
                'outline-gray-600 text-white active:outline-gray-700 hover:outline-gray-700' : ButtonVariant.default == variant && outlined,
                'outline-teal-300 text-white active:outline-teal-500 hover:outline-teal-500' : ButtonVariant.primary == variant && outlined,
                'outline-orange-400 text-white active:outline-oraginge-600 hover:outline-oraginge-600' : ButtonVariant.secondary == variant && outlined,
                'outline-teal-400 text-white active:outline-teal-500 hover:outline-teal-500' : ButtonVariant.success == variant && outlined,
                'outline-cyan-300 text-white active:outline-cyan-500 hover:outline-cyan-500' : ButtonVariant.info == variant && outlined,
                'outline-amber-400 text-white active:outline-amber-500 hover:outline-amber-500' : ButtonVariant.warning == variant && outlined,
                'outline-red-400 text-white active:outline-red-500 hover:outline-red-500' : ButtonVariant.danger == variant && outlined,
            })
        }>
            {label}
        </button>
    )
}